<template>
  <header class="header-block" :class="{ scrolled: scrollPosition > 0 && windowWidth > 1100}">
	  <div class="block">
		<parallax :fixed="false" class="position-absolute">
			<img src="../assets/images/mpc.jpg" alt="gdgdg">
		</parallax>
		<div class="container-fluid h-100 dark_background" slot="loading">

			<div class="h_card">
				<div class="row">
					<div class="h_card-column">
						<div class="myself">
						</div>
						<div class="social text-center pt-4">
							<a href="#" class="icon ml-2 mr-2 d-inline-block"><img src="../assets/images/facebook.svg" alt=""></a>
							<a href="#" class="icon ml-2 mr-2 d-inline-block"><img src="../assets/images/twetter.svg" alt=""></a>
							<a href="#" class="icon ml-2 mr-2 d-inline-block"><img src="../assets/images/github.svg" alt=""></a>
						</div>
					</div>
					<div class="h_card-column pl-5">
						<div class="h_card-row pb-3">
							<span class="title-lable bg-white">Who i am?</span>
							<h1>
								{{ header }}
								<span class="second_row d-block">{{ sub_title }}</span>
							</h1>
						</div>
						<div class="h_card-row pb-3">
							<p class="description mt-4">
								Lorem, ipsum dolor sit amet consectetur adipisicing elit. Nulla fugiat aliquam, ullam quis ab optio doloremque sint aut, eveniet dignissimos incidunt obcaecati unde adipisci ad odit rem impedit, inventore sequi!
							</p>
						</div>
						<a :href="this.download" class="download btn text-white" download>download CV<i class="fas fa-download ml-4"></i></a>
					</div>
				</div>
			</div>
		</div>
	  </div>
  </header>
</template>

<script>
import axios from 'axios'
import Parallax from 'vue-parallaxy'

export default {
	components: {
		Parallax
	},
	name: 'Aboute',
	data () {
		return {
			download: require('../assets/downloads/file.docx'),
			scrollPosition: null,
			windowWidth: 0,
			header: 'Yurii Scherbachenko',
			sub_title: "i'm a frontend developer",
			description: 'Выполняю Html-верстку (стандарты W3C, валидный код), создаю html-письма для e-mail рассылок. Разрабатываю с нуля сайты различной тематики: визитки, блоги, интернет-магазины, Landing Page и т.д., выполняю редизайн. Есть опыт кросбраузерной и адаптивной верстки, ваш сайт будет корректно работать на разных устройствах. Цена зависит от сложности макета и сроков.'
		}
	},
	methods: {
		getWindowWidth(event) {
			this.windowWidth = document.documentElement.clientWidth;
		},
		updateScroll: function() {
			this.scrollPosition = window.scrollY
		},
		beforeDestroy() {
			window.removeEventListener('resize', this.getWindowWidth);
			window.removeEventListener('scroll', this.updateScroll);
		}
	},
	mounted() {
		this.$nextTick(function() {
			window.addEventListener('resize', this.getWindowWidth);	
			window.addEventListener('scroll', this.updateScroll);

			this.getWindowWidth()
			this.updateScroll()
		})
	},
}
</script>


