<template>
	<div class="projects">
		<h2 class="text-orange">{{ header }}</h2>
		<div class="container">
			<div class="conteiner-fluid pt-4">
				<div class="responsive">
					<div v-for="(example, exampleID) in image_info" :key="exampleID" class="slide">
						<div class="photoWrapper mb-4">
							<button class="showBtn" v-on:click="[ current_visible = !example.visible, current_src = example.src, current_link_to = example.link_to, current_title = example.title ]">
								<i class="fas fa-search-plus"></i>
								{{ buttonName }}
							</button>
							<a :href="example.link_to" class="goto bg-white"><i class="fas fa-external-link-alt"></i></a>
							<div class="image bg-violet">
								<img width="457" :src="example.src" alt="photo">
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
		<transition name="example-animation">
			<div class="aboute-project fixed-top" v-if="current_visible">
				<div class="container-fluid">
					<button class="showBtn" v-on:click="current_visible = !current_visible">
						<i class="fas fa-times"></i>
					</button>
					<div class="row">
						<div class="col-12 col-md-5 col-lg-4 mb-sm-4">
							<h2 class="text-center">{{ current_title }}</h2>
							<p>Lorem, ipsum dolor sit amet consectetur adipisicing elit. Corporis, facere repudiandae. Velit facere porro, consequuntur et possimus quod culpa distinctio vel, aliquid accusamus, at quaerat nihil nulla. Minima, commodi beatae?</p>
							<div class="button-look"><a :href="current_link_to">перейти</a></div>	
						</div>
						<div class="col-12 col-md-7 col-lg-8 pb-4">
							<img :src="current_src" alt="photo">
						</div>
					</div>
				</div>
			</div>	
		</transition>
	</div>
</template>

<script>
export default {
	name: 'Carousel',
	data () {
		return {
			header: 'Мои работы',
			buttonName: 'Посмотреть',
			current_visible: false,
			current_src: '',
			current_link_to: '',
			current_title: '',
			image_info: [
				{
					src: require('../assets/images/images/Screenshot_2019-01-10 Y S-min.jpg'),
					link_to: 'https://uhonogo.github.io/buildcomputer',
					title: 'Дизайн-студия',
					visible: false
				},
				{
					src: require('../assets/images/images/dark_image-min.jpg'),
					link_to: 'https://uhonogo.github.io/flowers',
					title: 'Продажа цветов',
					visible: false
				},
				{
					src: require('../assets/images/images/house-min.jpg'),
					link_to: 'https://uhonogo.github.io/house',
					title: 'Продажа домов',
					visible: false
				},
				{
					src: require('../assets/images/images/htmb-min.jpg'),
					link_to: 'https://uhonogo.github.io/star-toys',
					title: 'Детские игрушки',
					visible: false
				},
				{
					src: require('../assets/images/images/template_screenshot-min.jpg'),
					link_to: 'https://uhonogo.github.io/butterbroad',
					title: 'Лендинг',
					visible: false
				},
				{
					src: require('../assets/images/images/landing-min.jpg'),
					link_to: 'https://uhonogo.github.io/travel',
					title: 'Путешествия',
					visible: false
				}
			]
		}
	}
}
</script>



