<template>
	<div class="my_skills mb-5">
		<div class="h-100 pl-3 pt-4">
			<span class="title-lable bg-dark-blue text-white">Who i am?</span>
			<h2 class="text-orange">{{ header }}</h2>
			<div class="row">
				<!-- begin skill level -->
				<div class="skill-block" v-for="(skill, skillId) in skills" :key="skillId">
					<div class="row">
						<div class="col-auto p-0">
							<div class="skill_icon">
								<img :src="skill.image" :alt="skill.title">
							</div>
						</div>
						<div class="col p-0">
							<div class="skill_wrap">
								<h3 class="skill_title gray-bg-block"><span>#{{skill.title}}</span></h3>
								<div class="progress_block">
									<div class="row">
										<div class="col-auto p-0">
											<span class="emoji sad"><img src="../assets/images/svg/sad_icon.svg" alt="sad-icon"></span>
										</div>
										<div class="col">
											<div class="progress">
												<div class="progress-bar bg-orange" :style="{ width: skill.progress + '%' }" role="progressbar" :aria-valuenow="skill.progress" aria-valuemin="0" aria-valuemax="100"></div>
											</div>
										</div>
										<div class="col-auto p-0">
											<span class="emoji happy"><img src="../assets/images/svg/happy_icon.svg" alt="happy-icon"></span>
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
				<!-- END skill level -->
			</div>
		</div>
	</div>
</template>

<script>
export default {
	name: 'HomePage',
	data () {
		return {
			header: 'My skills',
			skills: [
				{
					title: 'coding',
					progress: '90',
					image: require('../assets/images/svg/coding.svg')
				},
				{
					title: 'design',
					progress: '85',
					image: require('../assets/images/svg/design.svg')
				},
				{
					title: 'speed',
					progress: '80',
					image: require('../assets/images/svg/speed.svg')
				},
				{
					title: 'optimization',
					progress: '97',
					image: require('../assets/images/svg/optimization.svg')
				},
			]
		}
	}
}
</script>



