<template>
	<div class="conteiner-fluid">
		<div class="content">
			<form id="signup-form" class="signup-form" v-on:submit.prevent="processForm">
				<h3 class="text-white mb-4">{{ desc }}</h3>
				<div class="form-group d-flex justify-content-between" :class="{opacity: !show_textArea}">
					<!-- name -->
					<div class="field">
						<label class="label" :class="{ 'to_top': !name_up }">name</label>
						<input type="text" class="input" v-model="name_input" @focus="name_up = !name_up" @blur="name_up = true">
					</div>
					<!-- email -->
					<div class="field">
						<label class="label" :class="{ 'to_top': !email_up }">e-mail</label>
						<input type="e-mail" class="input" v-model="email_input" @focus="email_up = !email_up" @blur="email_up = true">
					</div>
				</div>
				<div class="textArea d-flex">
					<textarea name="text"></textarea>
					<!-- submit button -->
					<div class="field">
						<button class="form-button">
							<i class="fas fa-arrow-alt-circle-right"></i>	
						</button>
					</div>
				</div>
			</form>
		</div>
	</div>
</template>

<script>
export default {
	data () {
	    return {
	      	desc: 'Write to me',
			name_up: true,
			email_up: true,
			show_textArea: true,
			name_input: 'name',
			email_input: 'e-mail'
	    }
	}
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="sass">
$dark: #2c3e50
$orange: #FF6700
h1, h2
	color: $dark
	font-weight: normal
	margin: 30px 0 40px
p
	font-size: 27.5pt
</style>



